/* -------------------------

Title: Visibilities 
Descr: any hiding or showin

--------------------------- */

// Hidden, only for items that need to be hidden from everything, including screen readers
.hidden {
  display: none;
}
.offscreen {
  position: absolute;
  z-index: -1;
}

.sr-only {
  display: block;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(1px);
  white-space: nowrap;
  position: absolute;
}
