/* -------------------------

Title: _card--person.scss
Descr: Sets all specific styles for an person card
  sets css for card--feature as they are person

--------------------------- */

.person--card {
  display: grid;
  align-content: flex-start;
  figure {
    background: none;
  }
  .person__name-title {
    font-size: var(--base-28);
    font-weight: var(--semi);
    margin: 0 1rem;
  }
  .person__name {
    font-size: var(--base-28);
    font-weight: var(--semi);
  }
  .person__title {
    font-size: var(--base-20);
    margin-top: 0;
  }
  .person__contacts {
    font-size: var(--base-18);
    padding: 1rem;
    display: none; // hidden unless in a list view on directory
  }
  .person__copy {
    margin: 0 1rem;
  }
  .person__departments,
  .person__responsibilities {
    margin-top: 1em;
    padding-top: 1em;
    font-size: var(--base-16);
    border-top: 1px solid var(--as-teal);
    padding-bottom: 1rem;
    margin: 1rem;
    ul {
      padding-left: 1rem;
      margin-left: 0;
      margin-top: 0;
    }
  }
}
.card--small {
  border: none;
  margin-bottom: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  background: none;
  box-shadow: none;
  // max-width: 322px;
  :before {
    display: none;
  }
  .card--person__name-title {
    margin: 0;
    flex: 1 0 60%;
  }
  .card--person__name,
  .card--person__title {
    padding-top: 0;
    margin-top: 0;
  }
  .card--person__name {
    font-weight: var(--semi);
    font-size: var(--base-20);
  }
  .card--person__title {
    font-size: var(--base-20);
    font-weight: var(--regular);
  }
  figure {
    flex: 0 0 30%;
    margin-right: 1rem;
    // @include boxShadow();
  }
  img {
    border-radius: var(--border-radius);
  }
  .card__copy {
    flex: 0 0 60%;
    padding: 0;
    font-size: var(--base-18);
  }
  .card--person__contacts {
    display: none;
  }
}
